

import { defineComponent } from 'vue';
import { RouteLocationRaw } from 'vue-router';
import QuizList from "../assets/QuizList.json"

// 제이슨에서 타입을 설정해줄수없어서
interface Quiz{
  id:number,
  question: string,
  answer : string,
  view:{
    number1: string;
    number2: string;
    number3: string;
    number4: string;
  },
  type:string,
  hint:string
}

export default defineComponent({
  name: 'HomeView',
  data(){
    return{
      errorMsg:"이름을 입력해주세요",
      userName:"",
      dataList: [] as Quiz[],
      selectRandom:"0",
      selectType:"전체",
      selectCount:1
    }
  },
  methods:{
    chk(){
      const regex = /^[가-힣]*$/;
      // 정규식 사용. 한글만 사용하게 할거임.영어못쓰게
      if(!regex.test(this.userName)){
        this.userName = this.userName.replace(/[^가-힣]*/,'');
      }
    },
    // 시작버튼 눌렀을때 데이터가 없으면 에러뜨게 
    // : string | undefined
    NameChk(){
      if(!this.userName){
        const errorEl = document.querySelector(".error");
        errorEl?.classList.remove('invisible','opacity-0','top-[48%]');
        errorEl?.classList.add('top-1/2','opacity-1')
        setTimeout(()=>{
          errorEl?.classList.add('invisible','opacity-0','top-[48%]');
          errorEl?.classList.remove('top-1/2','opacity-1');
        },2500);
      }else{
        const route : RouteLocationRaw = {
        name:"QuizView",
        // 쿼리를 날린다. 키값:this.벨류값
        // 데이터를 보내기전에 라우터기능 설정(라우터폴더-index.ts)->중계해줌
        // userName:this.userName -> 작명:this.userName 을 
        query:{
          userName:this.userName,
          // 선택한 데이터를 퀴즈로 넘길거임
          selectRandom : this.selectRandom,
          selectType : this.selectType,
          selectCount :this.selectCount
        },
        replace:false, // 뒤로가기 할거면 replace:false
      }
        this.$router.push(route)
      }
    }
  },
  computed:{
    // 특정 속성을 추출해서 새로운 배열을 만들기 위해 set 객체를 사용한다. > 중복된 값을 허용하지 않는 데이터 집합 > 새로운 배열로 다시 반환
    // 우리가 원하는 배열에서 중복값을 제거하고 유일한 값을 배열로 생성.
    // 실제 나오는 값 html css js 
    // 여러개 나오는걸 제거하고 한개씩만 . 
    // 예) 남남남남 여여 -> 남 여 로만 출력 
    // uniqueTypes 문제유형
    uniqueTypes():string[]{
      return [...new Set(this.dataList.map((e)=>e.type))]
    },
    // item.length에 몇개있는지 파악해야함. 
    // 
    cateCount():number[]{
      return this.uniqueTypes.map((e)=>{
        return this.dataList.filter((item)=>{
          return e === item.type
        }).length
      })
    },
    // 이 방법은 페스티벌에서 사용했었다.
    cateList() : Quiz[]{
      return this.dataList.filter((e)=>{
        // selectType 은 위에 v-model
        // selectType이 전체, html,css,js
        // html 라면 html 이 나오고 전체라면 전부~
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{ 
          return e.type
        }
      })
    }
  },
  created(){
    this.dataList = QuizList.QuizList as Quiz [];
  },
  mounted(){
    document.querySelector("input")?.focus();
  }
});
