
import { defineComponent } from 'vue'
import QuizList from "../assets/QuizList.json"

interface QuizType{
  id:number,
  question:string,
  answer:string,
  view:{
    number1: string;
    number2: string;
    number3: string;
    number4: string;
  },
  type:string
  
}

export default defineComponent({
  neme:"QuizView",
  data(){
    return{
      dataList:QuizList,
      current:0,
      // as 문법
      userSelect : [] as string[],
      // Type Assertiom - 변수명 as 변경할타입 - vue 에서는 값 as 변경할타입

      /*
      as 키워드는 유니온 타입 같은 복잡한 타입을 하나의 정확한 타입으로 줄일 때 사용
      단, 하나의 타입일 때 사용시 에러 발생
      as 타입은 타입실드 임시해제용
      1. 왜 타입 에러가 나는지 모를 때 임시로 해결하기위해 사용하거나
      2. 내가 어떤 타입의 데이터가 들어올지 확실히 알고 있을때 에러를 방지 하기 위해 사용
      */ 
     hint:3,
    //  힌트를 눌렀을때 트루값, 힌트썼을때 상태를 체크
     hintUse:false,
    //  homeview.vue 에서 보낸 userName 받았음
    //  호출할땐 반드시 this 넣어줘야함.
     userName: this.$route.query.userName,
     selectRandom: this.$route.query.selectRandom,
     selectType: this.$route.query.selectType,
     selectCount: this.$route.query.selectCount,
     MaxCount:0
    }
  },
  methods:{
    //정답체크 클릭이벤트 메소드 추가
    // 타입 에러 unknown 설정 
    SelectValue(e : unknown){
      this.userSelect.push((e as string)[1]);
      console.log(this.userSelect)
    },
    useHint(): void{
      // if를 this밑에 적으니까 적용이 안되네 ?
      if(this.hintUse){return}
      if(this.hint < 1){return}
      this.hint--;
      this.hintUse ||= true;
      // 평가논리 계산법 - 값이 false 일때 true로만 변경 가능 이외에 값은 변경되지 않는다. 만약 여러가지 조건문이 필요하다면 if문을 사용 권장.
      // if(!this.hintUse){
      //   this.hintUse = true
      // }
      },
    questionCount(){
      this.MaxCount = this.dataList.QuizList.filter((e)=>{
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{
          return e.type
        } 
      }).length
    }
  },
  computed:{
    //몇퍼센트인지 체크
    progress():number{
      // 계산기할때 사용했음 . 자주 쓰인다.
      return Math.floor((this.current / Number(this.selectCount))*100);
      // progress 라는 이름의 타입을 넘버로 설정
      // 해당값을 리턴하는 값은 나머지를 계산하지않고 버림
      // 그 계산값은 현재의 문제 번호/ 문제의 개수 그 값을 나눈다음 * 100을 곱한다.
      // 0 / 4 * 100 = 0
      // 1 / 4 * 100 = 25
      // 2 / 4 * 100 = 50
      // 3 / 4 * 100 = 75
      // 4 / 4 * 100 = 100

      // floor - 소수점 나머지를 내림
      // ceil - 소수점 올림
      // round - 소수점 반올림
      /* 
      const num = 3.14592;
      const ceil = Math.ceil(num) - 4 (뒤에 소수점 몇이던 관심없이 무조건 +1)
      const floor - Math.floor(num) - 3 (뒤에 소수점 몇이던 관심없이 그냥 없앰)
      const round - Math.round(num) - 3 (만약에 3.54592 면 4가 나옴 )
      */
    },
    resultScore() : number{
  
      // 선택한 답과 실제 답 걸러낼거임
      return this.selectQuestion.filter((e,index)=>{
        return e.answer === this.userSelect[index]
        // 내가 선택한 값과 유저셀렉트값이 같다면 갯수를 리턴 
      }).length
    },
    //실시간으로 문제가 바뀌게 할거임
    //오브젝트의 값만 리턴시키는 Object.entries
    // -0.5 는 소수점 다 날리고 랜덤값 주기위해서 ? 소수점0.5이하버리겟다.12345를 랜덤으로 돌리겠다 ?
    // 스크립트에서 이렇게 만들어졌다 ?
    randomView() :Array<Array<[string,unknown]>>{
      return this.selectQuestion.map((e,index)=>{
        return Object.entries(this.selectQuestion[index].view).sort(()=>Math.random() - 0.5)
      })
    },
    selectQuestion() : QuizType[]{
      return this.dataList.QuizList.filter((e)=>{
        if(this.selectType !== '전체'){
          return e.type === this.selectType
        }else{
          // 전체면 타입을 반환
          return e.type
        } 
      })
    }
  },
  // 문제도 랜덤으로 돌리기위해
  created(){
    // 버그 ;위치땜에 동작안했음
    if(Number(this.selectRandom) != 0){
      this.selectRandom = "1";
    }

    // 랜덤으로 선택했을 때 문제가 바뀌지않는다. 보기는 바뀌지않는다.
    if(this.selectRandom == "1"){
      this.dataList.QuizList.sort(()=> Math.random() - 0.5)
    }
    // this.dataList.QuizList = this.selectQuestion;
    this.questionCount()

    // 버그 
    if(Number(this.selectCount) > this.MaxCount){
      this.selectCount = this.MaxCount.toString();
    }
  }
})
